<template>
  <div class="search">
    <h2>NASA API</h2>
    <h2>Type a name of planet or everything that connected to NASA</h2>
    <h5>Example : Moon, Sun, Mars, Asteroid and etc.</h5>
    <br>
    <form v-on:submit.prevent="getResult(query)">
      <input type="text" v-model="query" placeholder="Type in your search" class="form-control">
    </form>
    <br>
    <div v-if="results" class="results">
      <div v-for="result in results">
        <img v-bind:src="result.links[0].href">
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios';
export default {
  name: 'search',
  data () {
    return {
      msg: 'Search',
      query: '',
      results: ''
    }
  },
  methods: {
    getResult(query) {
      axios.get('https://images-api.nasa.gov/search?q='+ query + '&media_type=image')
        .then(response => {
          console.log(response.data.collection.items);
          this.results = response.data.collection.items;
        });
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.results img{
  height: 300px;
  margin: 10px;
}

h1, h2, h5 {
  font-weight: normal;
  color: white;
}

ul {
  list-style-type: none;
  padding: 0;
}

li {
  display: inline-block;
  margin: 0 10px;
}

a {
  color: white;
}
</style>



